
.Music {
  background-image: url();
  font-size: 16px;
  position: fixed;
  z-index: 2;
  top: 0.5rem;
  left: 5.4rem;
  width: 0.5rem;
}
.rotate {
  -webkit-animation: rotating 1.2s linear infinite;
}
@-webkit-keyframes rotating {
from {
    -webkit-transform: rotate(0)
}
to {
    -webkit-transform: rotate(360deg)
}
}
@keyframes rotating {
from {
    -webkit-transform: rotate(0);
            transform: rotate(0)
}
to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg)
}
}



